// =================================== topic index page start =====================================
.p-topic {
    .c-title {
        background-image: url(../img/topic/index/banner.jpg);
    }
}

.p-topicSearch {
    .c-section-title {
        margin-bottom: 27px !important;
    }
}

// =================================== topic index page end =====================================
// =================================== topic article page start =====================================
.p-topicArticle {
    .writer {
        margin-top: 20px;
        border: 1px solid #E6E6E6;
        padding: 30px 40px;
        display: flex;
        figure {
            width: 160px;
            padding-right: 40px;
        }
        .txtarea {
            width: calc(100% - 160px);
        }
        .job {
            font-family: $font_futura;
            font-weight: 500;
            font-style: italic;
            font-size: 14px;
            letter-spacing: 0.06em;
            line-height: (17 / 14);
            color: #00bedc;
        }
        .name {
            font-family: YuGothic;
            font-weight: bold;
            font-size: 18px;
            line-height: (28 / 18);
        }
        .txt {
            font-family: YuGothic;
            font-weight: 500;
            font-size: 13px;
            line-height: (22 / 13);
            margin-top: 11px;
        }
        .sns {
            margin: 14px 0;
            .twitter {
                width: 16px;
            }
            .instagram {
                width: 16px;
            }
            .facebook {
                width: 8.36px;
            }
            img {
                margin-right: 16px;
            }
        }
        .link {
            a {
                font-family: YuGothic;
                font-weight: 500;
                font-size: 12px;
                line-height: (22 / 12);
                color: #00bedc;
            }
        }
    }
    &_list {
        margin-top: 120px;
        .c-list {
            .item {
                width: 100%;
                display: flex;
                position: relative;
                border-bottom: 1px solid #E6E6E6;
                margin-bottom: 30px;
                padding-bottom: 30px;
                figure {
                    width: 240px;
                    padding-right: 30px;
                }
                .txtarea {
                    width: calc(100% - 240px);
                    .desc {
                        margin: 0 0 7px;
                    }
                    .category span {
                        line-height: (20 / 12);
                    }
                }
            }
        }
    }
}

// =================================== topic article page end =====================================
@include mq(sp) {
    .p-topicIndex {
        &-Ad2 {
            padding-bottom: 0;
        }
    }
    .p-topicArticle {
        .c-content {
            padding-top: 0;
            .column2 {
                padding: 0 37px;
                .spAd {
                    padding: 15px 0;
                }
            }
        }
        .writer {
            padding: 30px 20px 28px 19px;
            figure {
                width: 76px;
                padding-right: 16px;
            }
            .txtarea {
                width: calc(100% - 76px);
            }
        }
        &_list {
            margin-top: 85px;
            margin-bottom: 50px;
            .c-list {
                overflow-x: scroll;
                flex-wrap: unset;
                .item {
                    width: 200px;
                    flex-direction: column;
                    margin: 0 15px;
                    border: unset;
                    .txtarea {
                        width: 100%;
                        .desc {
                            font-size: 14px;
                        }
                    }
                }
            }
            .link {
                text-align: right;
                margin-right: 30px;
                a {
                    padding-bottom: 3px;
                    border-bottom: 1px solid black;
                    font-family: YuGothic;
                    font-weight: bold;
                    font-size: 13px;
                    line-height: (17 / 13);
                }
            }
        }
    }
}