// component start
.c-category {
    &::before {
        @include ct;
        content: "";
        position: absolute;
        padding: 2px 6px;
    }
    &-topic::before {
        content: "TOPICS";
    }
    &-event::before {
        content: "EVENT";
    }
    &-feature::before {
        content: "FEATURE";
    }
    &-info::before {
        content: "INFO";
    }
    &-spot::before {
        content: "SPOT";
    }
}

.c-pr {
    &::after {
        content: "PR";
        position: absolute;
        top: 38px;
        left: 55px;
        @include pr;
    }
}

.ct {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    .ctl {
        @include ct;
    }
    .prl {
        @include pr;
    }
}

.c-date {
    font-family: Lato;
    font-weight: 500;
    font-size: 12px;
    letter-spacing: 0.06em;
    line-height: (25 / 12);
    color: #b3b3b3;
}

.c-date1 {
    font-size: 13px;
    dd {
        font-family: Lato;
    }
}

.c-link {
    display: inline-block;
    background-color: black;
    color: white;
    font-weight: bold;
    font-size: 14px;
    letter-spacing: 0.1em;
    line-height: (25 / 14);
    color: #fff;
    padding: 9px 0 7px;
    text-align: center;
    outline: unset;
    border: unset;
    cursor: pointer;
    &::after {
        @include icon_img(6px, 8px, '../img/common/icon_arrow_r.svg');
        margin-left: 20px;
    }
    &.md {
        width: 300px;
    }
    &.sm {
        width: 242px;
    }
    &.white {
        width: 134px;
        color: #000;
        background-color: white;
        &::after {
            @include icon_img(6px, 8px, '../img/common/icon_arrow_rw.svg');
        }
    }
    &.blue {
        width: 134px;
        color: white;
        background-color: #00BEDC;
    }
    &.back {
        color: #000;
        background-color: white;
        border: 1px solid black;
        &::before {
            @include icon_img(6px, 8px, '../img/common/icon_arrow_rw.svg');
            transform: rotate(180deg);
            margin-right: 17px;
        }
    }
    @include hover_opacity();
}

.c-section-title {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    padding-bottom: 9px;
    &.underline {
        border-bottom: 2px solid #000000;
    }
    div {
        display: flex;
        align-items: baseline;
    }
    h2 {
        font-family: $font_futura;
        font-weight: 500;
        font-style: italic;
        font-size: 38px;
        letter-spacing: 0.1em;
        line-height: (44 / 38);
    }
    p {
        font-family: YuGothic;
        font-weight: bold;
        font-size: 16px;
        letter-spacing: 0.1em;
        margin-left: 28px;
    }
    a {
        font-family: YuGothic;
        font-weight: bold;
        font-size: 13px;
        line-height: (17 / 13);
        color: #000;
        &::after {
            @include icon_img(6.4px, 8.96px, '../img/common/icon_arrow_rb.svg');
            margin-left: 10px;
        }
    }
}

.c-label {
    width: 287.5px;
    background-color: #00BEDC;
    padding: 7px 0 7px 20px;
    font-family: YuGothic;
    font-weight: bold;
    font-size: 18px;
    letter-spacing: 0em;
    line-height: 20px;
    color: #fff;
    position: relative;
    margin-bottom: 30px;
    &::after {
        position: absolute;
        top: 0;
        right: 0;
        content: "";
        width: 16px;
        height: 34px;
        background: white;
        clip-path: polygon(0 0, 100% 50%, 0 100%);
        z-index: 1;
        transform: rotate(180deg);
    }
    &.sm {
        width: 123px;
    }
}

.c-content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 57px;
    .column1 {
        width: 662px;
        .c-section-title {
            margin-bottom: 50px;
        }
        .breadcrumb {
            padding-top: 0;
            li {
                line-height: 1;
            }
        }
    }
    .column2 {
        width: 300px;
        .advertise {
            margin-bottom: 20px;
        }
        .ranking {
            padding-top: 40px;
        }
        .title {
            display: flex;
            align-items: baseline;
            padding-bottom: 9px;
            border-bottom: 2px solid #000000;
            h2 {
                font-family: $font_futura;
                font-weight: 500;
                font-style: italic;
                font-size: 28px;
                letter-spacing: 0.1em;
                line-height: (44 / 38);
            }
            p {
                font-family: YuGothic;
                font-weight: bold;
                font-size: 13px;
                letter-spacing: 0.1em;
                margin-left: 28px;
            }
        }
        .item {
            display: flex;
            padding: 28.6px 0 21.4px;
            border-bottom: 1px solid #E6E6E6;
            position: relative;
            h3 {
                margin-right: 19px;
                font-family: $font_futura;
                font-weight: 500;
                font-style: italic;
                font-size: 26px;
                line-height: 0.8;
            }
            .txtarea {
                margin-left: 13px;
            }
            .desc {
                font-weight: bold;
                font-size: 14px;
                letter-spacing: 0.1em;
                line-height: (20 / 14);
            }
            img {
                min-width: 80px;
                @include hover_opacity();
            }
            &::before {
                bottom: 26px;
                left: 0;
                z-index: 1;
            }
        }
        form {
            padding: 27px 38px 50px;
            background-color: #F7F7F4;
            .radio {
                display: flex;
                margin: 16px 0;
                .u-radio:first-child {
                    margin-right: 30px;
                }
            }
            .date {
                display: flex;
                justify-content: space-between;
                align-items: center;
                input {
                    padding: 5px 10px;
                }
                .year {
                    width: 70px;
                }
                .month {
                    width: 40px;
                }
                .day {
                    width: 40px;
                }
                &.first {
                    margin-bottom: 9px;
                }
            }
            .u-select {
                margin-top: 6px;
                select {
                    width: 100%;
                }
            }
            dl {
                padding: 17px 0 20px;
                &:not(:last-child) {
                    border-bottom: 2px solid white;
                }
                dt {
                    font-family: YuGothic;
                    font-weight: bold;
                    font-size: 15px;
                    line-height: 2;
                }
            }
            .c-link {
                width: 100%;
            }
        }
    }
}

.c-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .item {
        width: 300px;
        margin-bottom: 50px;
        position: relative;
        img {
            @include hover_opacity();
        }
        .desc {
            font-family: YuGothic;
            font-weight: bold;
            font-size: 16px;
            line-height: (26 / 16);
            margin: 20px 0 13px;
        }
        dl {
            display: flex;
            align-items: center;
            margin-bottom: 14px;
            dt {
                display: flex;
                align-items: center;
                &::after {
                    content: "";
                    width: 1px;
                    height: 20px;
                    display: block;
                    background-color: #383838;
                    margin: 0 10.5px;
                }
            }
        }
        .category {
            margin-bottom: 10px;
            span {
                background-color: #F7F7F4;
                font-family: YuGothic;
                font-weight: 500;
                font-size: 12px;
                line-height: (25 / 12);
                color: #383838;
                padding: 2px 8px;
                margin: 0 5px 5px 0;
                display: inline-block;
            }
        }
        .area {
            span {
                font-family: YuGothic;
                font-weight: 500;
                font-size: 12px;
                line-height: (18 / 12);
                color: #00bedc;
                border-bottom: 1px solid #00BEDC;
                margin-bottom: 8px;
                display: inline-block;
                &:not(:last-child) {
                    margin-right: 10px;
                }
            }
        }
        .c-date {
            padding-top: 7px;
        }
        &.new {
            &::before {
                @include icon_img(66.44px, 63.04px, '../img/common/icon_new.svg');
                position: absolute;
                top: -27px;
                left: -10px;
            }
        }
        &.pr {
            &::after {
                content: "PR";
                font-family: YuGothic;
                font-weight: bold;
                font-size: 10px;
                letter-spacing: 0.1em;
                line-height: 1.5;
                padding: 6px 11px;
                background: #fff;
                opacity: 0.7;
                position: absolute;
                top: 0;
                right: 0;
            }
        }
        .mark_date {
            display: block;
            position: absolute;
            left: -5px;
            top: 10px;
            background-color: #00BEDC;
            padding: 2px 10px;
            font-family: YuGothic;
            font-weight: bold;
            font-size: 12px;
            line-height: 1.3;
            color: #fff;
            z-index: 2;
        }
    }
}

.c-title {
    background-size: cover;
    background-repeat: no-repeat;
    text-align: center;
    color: #fff;
    height: 163px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    h2 {
        font-family: $font_futura;
        font-weight: 500;
        font-style: italic;
        font-size: 50px;
        letter-spacing: 0.1em;
        line-height: 1.1;
    }
    p {
        font-family: YuGothic;
        font-weight: bold;
        font-size: 16px;
        letter-spacing: 0.1em;
        line-height: 1.5;
    }
}

.c-form {
    padding: 30px 0;
    background-color: #F7F7F4;
    .c-section-title {
        padding-left: 38px;
        margin-bottom: 0 !important;
        h2 {
            font-size: 28px;
            letter-spacing: 0.1em;
            line-height: (44 / 28);
        }
        p {
            font-size: 15px;
            letter-spacing: 0.1em;
        }
    }
    .u-select {
        width: 241.21px;
    }
    .content {
        padding: 20px 38.5px 0;
    }
    dl {
        padding: 20px 0;
        display: flex;
        align-items: center;
        border-bottom: 1px solid white;
        dt {
            font-family: YuGothic;
            font-weight: bold;
            font-size: 15px;
            line-height: 2;
            width: 180px;
        }
        dd {
            width: calc(100% - 180px);
        }
    }
    .link {
        .c-link {
            margin: 30px 0 0 180px;
        }
    }
}

.c-article {
    .breadcrumb {
        padding: 0;
    }
    .main {
        padding: 78px 0 31px;
        border-bottom: 1px solid #E6E6E6;
        position: relative;
        h2 {
            font-family: YuGothic;
            font-weight: bold;
            font-size: 22px;
            line-height: (33 / 22);
        }
        .c-date {
            padding: 12px 0 20px;
        }
        .category {
            margin: 18px 0 10px;
            span {
                background-color: #F7F7F4;
                font-family: YuGothic;
                font-weight: 500;
                font-size: 12px;
                line-height: (25 / 12);
                color: #383838;
                padding: 2px 8px;
                margin: 0 5px 5px 0;
                display: inline-block;
            }
        }
        .area {
            span {
                font-family: YuGothic;
                font-weight: 500;
                font-size: 12px;
                line-height: (18 / 12);
                color: #00bedc;
                border-bottom: 1px solid #00BEDC;
                margin-bottom: 8px;
                display: inline-block;
                &:not(:last-child) {
                    margin-right: 10px;
                }
            }
        }
        .vote {
            margin-top: 29px;
        }
        &.c-category {
            &::before {
                top: 38px;
                left: 0;
            }
        }
        &.c-pr {
            &::before {}
        }
    }
    .content1 {
        padding-top: 36px;
        h3 {
            font-family: YuGothic;
            font-weight: bold;
            font-size: 20px;
            line-height: (28 / 20);
        }
        p {
            font-family: YuGothic;
            font-weight: 500;
            font-size: 15px;
            line-height: (27 / 15);
            margin-top: 27px;
        }
    }
    .content2 {
        padding: 72px 0 57px;
        h3 {
            font-family: YuGothic;
            font-weight: bold;
            font-size: 18px;
            line-height: (32 / 18);
            color: #00bedc;
            display: flex;
            align-items: center;
            border-left: 7px solid #00BEDC;
            padding-left: 5px;
        }
        .txt {
            font-family: YuGothic;
            font-weight: 500;
            font-size: 15px;
            line-height: (27 / 15);
            margin-top: 24px;
        }
        .pic {
            display: flex;
            justify-content: center;
            margin-top: 82px;
            &_inner {
                width: 380px;
            }
            p {
                font-family: YuGothic;
                font-weight: 500;
                font-size: 12px;
                line-height: calc(20 / 12);
                color: #383838;
                text-align: left;
                margin-top: 12px;
            }
        }
    }
    .content3 {
        h3 {
            font-family: YuGothic;
            font-weight: bold;
            font-size: 18px;
            line-height: (28 / 18);
        }
        .txt {
            font-family: YuGothic;
            font-weight: 500;
            font-size: 15px;
            line-height: (27 / 15);
            margin-top: 21px;
        }
        margin-bottom: 112px;
    }
    .info {
        position: relative;
        &::before {
            width: 0;
            height: 0;
            border-top: 0px solid transparent;
            border-right: 30px solid white;
            border-bottom: 30px solid transparent;
            content: "";
            top: 0;
            right: 0;
            position: absolute;
        }
        padding: 24px 0;
        &.info1 {
            background-color: #F7F7F4;
        }
        &.info2 {
            background-color: #ECF0F2;
            margin-top: 20px;
        }
        .c-section-title {
            padding: 0 20px 3px;
            margin-bottom: 30px;
            h2 {
                font-size: 20px;
                letter-spacing: 0.06em;
                line-height: 1.5;
            }
            p {
                font-size: 14px;
                line-height: 2;
            }
        }
        .content {
            display: flex;
            padding: 0 40px;
            width: 100%;
            figure {
                padding-right: 40px;
                width: 209px;
            }
            .txtarea {
                width: calc(100% - 209px);
                h3 {
                    font-family: YuGothic;
                    font-weight: bold;
                    font-size: 18px;
                    line-height: (28 / 18);
                    margin-bottom: 11px;
                }
                dl {
                    display: flex;
                    font-weight: 500;
                    font-size: 13px;
                    line-height: 1.5;
                    color: #383838;
                    &:not(:last-child) {
                        margin-bottom: 6px;
                    }
                    dt {
                        font-family: YuGothic;
                        width: 85px;
                    }
                    dd {
                        font-family: Lato;
                        width: calc(100% - 85px);
                    }
                }
                .link {
                    margin-top: 30px;
                    .c-link {
                        width: 207px;
                    }
                }
            }
        }
    }
    &_list {
        margin-top: 90px;
        .c-list {
            .item {
                width: calc((100% - 60px) / 3);
                margin-bottom: 10px;
            }
        }
        .link {
            display: none;
            text-align: right;
            margin-right: 30px;
            a {
                padding-bottom: 3px;
                border-bottom: 1px solid black;
                font-family: YuGothic;
                font-weight: bold;
                font-size: 13px;
                line-height: (17 / 13);
            }
        }
    }
}

.c-content1 {
    margin-top: 58px;
}

.c-other-ttl {
    color: black;
    text-align: center;
    margin-bottom: 58px;
    h2 {
        font-family: $font_futura;
        font-weight: 500;
        font-style: italic;
        font-size: 40px;
        letter-spacing: 0.1em;
        line-height: 44px;
        span:nth-child(2) {
            margin-left: 20px;
        }
    }
    p {
        font-family: YuGothic;
        font-weight: bold;
        font-size: 16px;
        letter-spacing: 0.1em;
    }
}

.c-result {
    font-family: YuGothic;
    font-weight: bold;
    font-size: 16px;
    letter-spacing: 0.1em;
    line-height: 1.5;
    margin-bottom: 52px;
}

.c-control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .control {
        display: flex;
        align-items: center;
        margin-bottom: 25px;
        a {
            font-family: YuGothic;
            font-weight: bold;
            font-size: 13px;
            line-height: (17 / 13);
            display: block;
            padding: 0 6px;
            border-bottom: 1px solid black;
            margin-right: 12px;
        }
        button {
            outline: unset;
            border: 1px solid #E6E6E6;
            width: 30px;
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            background-color: white;
            &.first {
                margin-right: 6px;
            }
        }
    }
}

.c-map-piece {
    position: relative;
    .item {
        position: absolute;
        &.active {
            path {
                fill: #66D8EA;
            }
        }
    }
    .item1 {
        top: 10px;
        left: 9px;
    }
    .item2 {
        top: 0;
        left: 92px;
    }
    .item3 {
        top: 0;
        left: 194px;
    }
    .item9 {
        top: 58px;
        left: 86px;
    }
    .item8 {
        top: 158px;
        left: 87px;
    }
    .item5 {
        top: 239px;
        left: 132px;
    }
    .item6 {
        top: 191px;
        left: 199px;
    }
    .item7 {
        top: 171px;
        left: 225px;
    }
    .item4 {
        top: 208px;
        left: 208px;
    }
    .item10 {
        top: 19px;
        left: 165px;
        position: absolute;
    }
}

.sp-control {
    display: none;
    text-align: right;
    a {
        padding-bottom: 3px;
        border-bottom: 1px solid black;
        font-family: YuGothic;
        font-weight: bold;
        font-size: 13px;
        line-height: (17 / 13);
    }
}

@include mq(sp) {
    .c-content {
        padding-top: 39px;
        .column1 {
            width: 100% !important;
        }
        .column2 {
            width: 100% !important;
            .advertise {
                display: none;
            }
            .ranking {
                padding-top: 0;
            }
        }
        &.default {
            .column1 {
                padding: 0 27px;
                .c-list {
                    justify-content: space-around;
                }
                .link {
                    padding: 0 10px;
                }
            }
            .column2 {
                padding: 0 37px;
            }
        }
    }
    .c-title {
        height: 85px;
        h2,
        p {
            visibility: hidden;
        }
    }
    .c-list {
        .item {
            margin-bottom: 40px;
        }
    }
    .c-form {
        .u-select {
            width: 100%;
        }
        .content {
            padding: 20px 20px 0;
        }
        dl {
            flex-wrap: wrap;
            flex-direction: column;
            dt {
                width: 100%;
            }
            dd {
                width: 100%;
            }
        }
        .link {
            text-align: center;
            .c-link {
                margin: 30px auto 0;
            }
        }
        .c-section-title {
            padding-left: 38px;
            div {
                flex-direction: column;
                p {
                    margin-left: 0;
                }
            }
        }
    }
    .c-article {
        &__inner {
            padding: 0 20px;
        }
        .info .content {
            flex-direction: column;
            padding: 0 20px;
            figure {
                padding-right: 0;
                width: 100%;
                text-align: center;
            }
            .txtarea {
                width: 100%;
                margin-top: 30px;
                .link {
                    text-align: center;
                }
            }
        }
        &_list {
            .link {
                display: block;
            }
            margin-top: 50px;
            .c-control {
                .control {
                    display: none;
                }
            }
            .c-list {
                overflow-x: scroll;
                flex-wrap: unset;
                .item {
                    min-width: 200px;
                    margin: 0 15px;
                    padding-bottom: 30px;
                }
            }
        }
    }
    .c-section-title {
        &.sp-column {
            div {
                flex-direction: column;
                p {
                    margin-left: 0;
                }
            }
        }
    }
    .c-control {
        .control {
            display: none;
        }
    }
    .sp-control {
        display: block;
    }
    .c-other-ttl {
        h2 {
            font-size: 32px;
        }
    }
}

// component end