.advertising-item {
    color: #000;
    margin-top: 70px;
    &:nth-child(1) {
        margin-top: unset;
    }
    &:nth-child(2) {
        .advertising-item__ttl p {
            border-left: 4px solid #00BEDC;
            padding-left: 8px;
            color: #00BEDC;
        }
    }
    &__ttl {
        p {
            font-family: YuGothic;
            font-weight: bold;
            font-size: 20px;
            line-height: 28px;
        }
    }
    &__txt {
        margin-top: 30px;
        p {
            font-family: YuGothic;
            font-weight: 500;
            font-size: 15px;
            line-height: 27px;
        }
    }
}

@include mq(sp) {
    .advertising {
        .container {
            padding: 0 20px;
        }
    }
}