.privacy {
    &-intro {
        margin-bottom: 70px;
        p {
            font-family: YuGothic;
            font-weight: 500;
            font-size: 15px;
            line-height: 27px;
            text-align: left;
            color: #000;
        }
    }
    &-item {
        margin-top: 45px;
        &__ttl {
            p {
                font-family: YuGothic;
                font-weight: bold;
                font-size: 20px;
                line-height: 28px;
                text-align: left;
                color: #000;
                span:nth-child(1) {
                    margin-right: 15px;
                }
            }
        }
        &__txt {
            margin-top: 30px;
            p {
                font-family: YuGothic;
                font-weight: 500;
                font-size: 15px;
                line-height: 27px;
                text-align: left;
                color: #000;
            }
        }
    }
}

@include mq(sp) {
    .privacy {
        .container {
            padding: 0 20px;
        }
        .c-other-ttl h2 span:nth-child(2) {
            margin-left: 0;
        }
    }
}