// =================================== common header start =====================================
.l-header {
    position: sticky;
    top: 0;
    z-index: 10;
    background-color: white;
    width: 100%;
    border-bottom: 1px solid #E6E6E6;
    a {
        @include hover_opacity();
    }
    .content {
        padding: 17.5px 0;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
    }
    .menu {
        .top {
            text-align: right;
            ul {
                justify-content: flex-end;
                li {
                    margin-left: 8px;
                }
            }
        }
        .bottom {
            margin: 13px 7px 0 0;
            a {
                font-weight: bold;
                font-size: 14px;
                line-height: (20 / 14);
            }
            ul {
                li {
                    margin-left: 50px;
                }
            }
        }
        .sp-menu {
            display: none;
            cursor: pointer;
            &__link {
                span {
                    width: 30px;
                    height: 2px;
                    background-color: #3B4043;
                    display: block;
                    border-radius: 5px;
                    &:nth-child(2) {
                        margin-top: 8px;
                        margin-bottom: 8px;
                    }
                }
            }
            &.change {
                display: none;
            }
        }
    }
    ul {
        display: flex;
        a {
            display: flex;
        }
    }
}

.overlay {
    overflow-y: auto;
    overflow-x: hidden;
    position: fixed;
    height: 100%;
    z-index: 99;
    -webkit-font-smoothing: antialiased;
    /* to stop flickering of text in safari */
    right: 0;
    transition: width 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0);
    width: 0;
    background-color: transparent;
    display: flex;
    justify-content: flex-end;
    .close-content {
        width: 24%;
    }
    .wrapper {
        overflow-y: auto;
        position: relative;
        width: 76%;
        min-height: 100vh;
        padding: 0px 0px 20px;
        background-color: black;
        color: white;
    }
    .close {
        cursor: pointer;
        margin: 21px 25px 0 0;
        width: 25px;
        height: 25px;
        position: relative;
        span {
            cursor: pointer;
            width: 30px;
            height: 2px;
            background-color: #3B4043;
            display: block;
            transition: 0.4s;
            &:nth-child(1) {
                transform: rotate( -45deg) translate(-10px, 7px);
            }
            &:nth-child(2) {
                transform: rotate( 45deg) translate(2px, 5px);
                margin-top: 5px;
            }
        }
    }
    .search {
        padding: 15px 14px 12px;
        position: relative;
        input {
            width: 100%;
            border-radius: 20px;
            outline: unset;
            border: unset;
            padding: 10.5px 16px;
            background-color: white;
        }
        button {
            position: absolute;
            top: 50%;
            right: 26px;
            transform: translateY(-50%);
            @include hover_opacity();
            cursor: pointer;
            outline: unset;
            border: unset;
            background-color: unset;
            display: flex;
            padding: 0;
        }
    }
    ul {
        li {
            border-bottom: 1px solid white;
            &:first-child {
                border-top: 1px solid white;
            }
            a {
                padding: 13px 21px 12px 20px;
                color: white;
                display: flex;
                width: 100%;
                justify-content: space-between;
                align-items: center;
                position: relative;
                font-family: YuGothic;
                font-weight: bold;
                font-size: 15px;
                line-height: (20 / 15);
                cursor: pointer;
            }
            .item {
                &::after {
                    @include icon_img(5px, 8.67px, '../img/common/icon_menu_arrow.svg');
                }
            }
            .collapsible {
                &::after {
                    @include icon_img(10px, 10px, '../img/common/icon_menu_open.svg');
                }
                &.active {
                    &::after {
                        @include icon_img(10px, 1px, '../img/common/icon_menu_close.svg');
                    }
                }
            }
            .content {
                max-height: 0;
                overflow: hidden;
                background-color: #535353;
                transition: max-height 0.2s ease-out;
                li {
                    border-bottom: 1px solid #8D8787;
                }
                a {
                    font-size: 14px;
                    line-height: (20 / 14);
                    padding-left: 50px;
                }
            }
        }
    }
    .sns {
        text-align: center;
        margin-top: 30px;
        a {
            &:not(:last-child) {
                img {
                    margin-right: 14px;
                }
            }
        }
    }
    &.change {
        width: 100%;
    }
}

@media (max-width: 1100px) {
    .l-header .content {
        padding: 17.5px;
    }
    .l-header .menu .bottom ul li {
        margin-left: 20px;
    }
}

@media (max-width: 900px) {
    .l-header .content .logo {
        width: 154px;
    }
    .l-header .menu .top,
    .l-header .menu .bottom {
        display: none;
    }
    .l-header .menu .sp-menu {
        display: inline-block;
    }
    .l-header .content {
        padding: 10px 24px 9px 20px;
        align-items: center;
    }
}

@include mq(sp) {
    .l-header {
        position: sticky;
    }
}

// =================================== common footer end =====================================