// =================================== spot index page start =====================================
.p-spot {
    .c-title {
        background-image: url(../img/spot/index/banner.jpg);
    }
    .c-content {
        .map {
            background-color: #F7F7F4;
            margin-bottom: 50px;
            dl {
                padding: 17px 0 8px;
                &:not(:last-child) {
                    border-bottom: 2px solid white;
                }
                dt {
                    display: flex;
                    padding-bottom: 20px;
                    h4 {
                        font-family: YuGothic;
                        font-weight: bold;
                        font-size: 18px;
                        letter-spacing: 0.06em;
                        line-height: (20 / 18);
                    }
                    p {
                        font-family: YuGothic;
                        font-weight: 500;
                        font-size: 13px;
                        line-height: (20 / 13);
                        margin-left: 20px;
                    }
                }
            }
            .link {
                text-align: center;
                margin-top: 22px;
            }
            .top {
                display: flex;
                .c-map-piece {
                    width: calc(100% - 260px);
                    height: 335px;
                    .inner {
                        zoom: 0.75;
                        text {
                            font-size: 16px;
                        }
                    }
                }
                &_controls {
                    width: 260px;
                    padding-top: 36px;
                    dd {
                        display: flex;
                        flex-wrap: wrap;
                    }
                    dd .item {
                        width: calc(100% / 2);
                    }
                }
            }
            .bottom {
                padding: 0 30px 30px;
                .row {
                    display: flex;
                    flex-wrap: wrap;
                }
                dd .item {
                    width: calc(100% / 4);
                }
            }
        }
    }
}

.p-spotArticle {
    .main {
        .slider-main {
            margin-top: 22px;
        }
        .slider-main img {
            width: 100%;
        }
        .slider-nav {
            padding: 20px 60px 0;
            .slick-slide {
                opacity: 0.7;
                &.slick-current {
                    opacity: 1;
                }
            }
        }
    }
    .c-article {
        .info {
            .content {
                display: block;
                .txtarea {
                    width: 100%;
                    dl {
                        line-height: 2;
                        &:not(:last-child) {
                            margin-bottom: 17px;
                        }
                        font-size: 14px;
                        dt {
                            width: 105px;
                        }
                        dd {
                            width: calc(100% - 105px);
                        }
                    }
                }
                .note {
                    font-family: YuGothic;
                    font-weight: 500;
                    font-size: 13px;
                    line-height: (20 / 13);
                    color: #f00;
                    margin: 29px 0;
                }
                iframe {
                    width: 100%;
                    height: 400px;
                }
            }
        }
    }
}

.p-spotSearch {
    .result {
        font-family: YuGothic;
        font-weight: bold;
        font-size: 16px;
        letter-spacing: 0.1em;
        line-height: 1.5;
    }
    .c-section-title {
        margin-bottom: 27px !important;
    }
}

@include mq(sp) {
    .p-spot {
        .c-content {
            .map {
                margin-bottom: 0;
                .top {
                    flex-wrap: wrap;
                    .c-map-piece {
                        width: 100%;
                        height: 255px;
                        .inner {
                            zoom: 0.65;
                        }
                    }
                }
                .top_controls {
                    width: 100%;
                    padding-left: 20px;
                }
                .bottom {
                    padding: 0 20px 30px;
                    dd {
                        .item {
                            width: calc(100% / 2);
                        }
                    }
                }
            }
        }
        .pagination {
            margin-bottom: 0;
        }
        &-spAd2 {
            padding-bottom: 0;
        }
        .spAd {
            padding: 40px 27px;
        }
    }
    .p-spotArticle {
        .c-article .info {
            &.info2 {
                padding-bottom: 0;
            }
            .content {
                padding: 0;
                .txtarea {
                    padding: 0 20px;
                    dl {
                        dt {
                            width: 83px;
                        }
                        dd {
                            width: calc(100% - 83px);
                        }
                    }
                }
                .note {
                    padding: 0 20px;
                }
                iframe {
                    height: 337px;
                }
            }
        }
    }
}

// =================================== spot index page end =====================================