// utility start
.container {
    padding: 0 0 120px;
}

.u-inner {
    width: 1025px;
    margin: auto;
}

.u-inner2 {
    width: 660px;
    margin: auto;
}

.u-radio {
    font-family: "Source Han Sans JP";
    font-weight: normal;
    font-size: 15px;
    line-height: 1.5;
    color: #323232;
    display: block;
    position: relative;
    padding-left: 30px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        &:checked {
            ~ {
                .radio-mark {
                    background-color: #2196F3;
                    &:after {
                        display: block;
                    }
                }
            }
        }
    }
    &:hover {
        input {
            ~ {
                .radio-mark {
                    background-color: #ccc;
                }
            }
        }
    }
    .radio-mark {
        &:after {
            top: 5px;
            left: 5px;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background: white;
        }
    }
}

.radio-mark {
    position: absolute;
    top: 2px;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: white;
    border-radius: 50%;
    border: 1px solid #D8D8D8;
    &:after {
        content: "";
        position: absolute;
        display: none;
    }
}

.u-input {
    outline: unset;
    border: 1px solid #D8D8D8;
    background-color: white;
    font-family: "Source Han Sans JP";
    font-weight: 500;
    font-size: 16px;
    line-height: (30 / 16);
    padding: 5px 20px;
    color: #323232;
    width: 100%;
    &::placeholder {
        color: #B3B3B3;
        font-family: YuGothic;
        font-weight: 500;
        font-size: 14px;
        line-height: 30px;
    }
}

.u-select {
    position: relative;
    &::after {
        @include icon_img (8px, 6px, '../img/common/icon_select_arrow.svg');
        position: absolute;
        top: 50%;
        right: 10px;
        transform: translateY(-50%);
    }
    select {
        outline: unset;
        border: 1px solid #D8D8D8;
        background-color: white;
        font-family: "Source Han Sans JP";
        font-weight: 500;
        font-size: 16px;
        line-height: (30 / 16);
        padding: 5px 20px;
        color: #323232;
        -webkit-appearance: none;
        -moz-appearance: none;
        text-indent: 1px;
        text-overflow: '';
        width: 100%;
    }
}

.u-checkbox {
    display: block;
    position: relative;
    padding-left: 33px;
    margin-bottom: 12px;
    cursor: pointer;
    font-family: "Source Han Sans JP";
    font-weight: normal;
    font-size: 13px;
    line-height: 1.5;
    color: #323232;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
        &:checked {
            ~ {
                .checkmark {
                    background-color: white;
                    &:after {
                        display: block;
                    }
                }
            }
        }
    }
    &:hover {
        input {
            ~ {
                .checkmark {
                    background-color: white;
                }
            }
        }
    }
    .checkmark {
        &:after {
            left: 6px;
            top: 1px;
            width: 8px;
            height: 12px;
            border: solid #00BEDC;
            border-width: 0 2px 2px 0;
            -webkit-transform: rotate(45deg);
            -ms-transform: rotate(45deg);
            transform: rotate(45deg);
        }
    }
}

.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    border-radius: 2px;
    border: 1px solid #D8D8D8;
    background-color: white;
    &:after {
        content: "";
        position: absolute;
        display: none;
    }
}

.breadcrumb {
    padding: 13px 0;
    list-style: none;
    li {
        display: inline;
        font-family: YuGothic;
        font-weight: 500;
        font-size: 12px;
        line-height: (33 / 12);
        color: #00BEDC;
        a {
            color: #808080;
            &:hover {
                color: #01447e;
                text-decoration: underline;
            }
        }
    }
    li+li {
        &:before {
            margin: 0 10px;
            @include icon_img (6px, 8px, '../img/common/icon_breadcrumb.svg');
        }
    }
}

.pagination {
    display: flex;
    justify-content: center;
    margin-bottom: 58px;
    a {
        color: black;
        padding: 8px 13px;
        font-family: YuGothic;
        font-weight: 500;
        font-size: 14px;
        line-height: (24 / 14);
        text-align: center;
    }
    a.active {
        color: #00BEDC;
    }
}

.blue {
    color: #00BEDC;
}

.spAd {
    padding: 50px 0;
    img {
        width: 100%;
    }
    display: none;
}

@media (max-width: 1025px) {
    body {
        width: 1025px;
        overflow-x: scroll;
        .u-inner {
            width: 100%;
        }
    }
}

@include mq(sp) {
    .sp-hidden {
        display: none !important;
    }
    // html {
    //     overflow-x: hidden;
    // }
    body {
        width: 100%;
        overflow-x: hidden;
    }
    .breadcrumb {
        display: none;
    }
    .spAd {
        display: block;
    }
    .u-inner2 {
        width: 100%;
    }
}

@include mq(pc) {
    .pc-hidden {
        display: none !important;
    }
}

// utility end