// =================================== mixin start =====================================
$breakpoints: ( 'sp': 'only screen and (max-width: 768px)', 'tb': 'print,screen and (max-width: 1280px)', 'pc': 'print,screen and (min-width: 769px)') !default;
@mixin mq($breakpoint: md) {
    @media #{map-get($breakpoints, $breakpoint)} {
        @content;
    }
}

@mixin icon_img($w: 15px, $h: 15px, $i: "assets/img/common/logo.svg") {
    content: "";
    display: inline-block;
    background: url($i) top center/cover;
    width: $w;
    height: $h;
    @content;
}

@mixin hover_opacity($c: .7) {
    transition: .3s ease-in-out;
    &:hover {
        opacity: $c;
    }
    @include mq(sp) {
        transition: unset;
        &:hover {
            opacity: 1;
        }
    }
}

@mixin ct {
    font-family: $font_futura;
    font-weight: 500;
    font-style: italic;
    font-size: 12px;
    letter-spacing: 0.06em;
    line-height: 1.41667;
    color: #fff;
    background-color: black;
    display: inline-block;
    padding: 2px 6px;
    @include mq(sp) {
        font-size: 10px;
    }
}

@mixin pr {
    font-family: $font_futura;
    font-weight: 500;
    font-style: italic;
    font-size: 12px;
    letter-spacing: 0.06em;
    line-height: 1.41667;
    color: #fff;
    background: #707070;
    padding: 2px 6px;
    display: inline-block;
    @include mq(sp) {
        font-size: 10px;
        left: 47px;
    }
}

// =================================== mixin end =====================================