// =================================== movingguide index page start =====================================
.p-movingguide {
    background-color: #F7F7F4;
    .c-title {
        background-image: url(../img/movingguide/index/banner.jpg);
    }
    .otherinfo {
        position: relative;
        margin-top: 96px;
        .bg {
            width: 100%;
            height: 200px;
            position: absolute;
            top: -40px;
            background-color: #FFFFFF;
        }
        .title {
            color: black;
            display: flex;
            flex-wrap: wrap;
            align-items: baseline;
            position: relative;
            z-index: 2px;
            margin-bottom: 20px;
            h3 {
                font-family: $font_futura;
                font-weight: 500;
                font-style: italic;
                font-size: 30px;
                letter-spacing: 0.1em;
                line-height: (44 / 30);
            }
            p {
                font-family: YuGothic;
                font-weight: bold;
                font-size: 16px;
                letter-spacing: 0.1em;
                line-height: 1.5;
                margin-left: 30px;
            }
        }
        .list {
            position: relative;
            z-index: 2;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            .item {
                width: calc((100% - 64px) / 3);
                position: relative;
                border: 2px solid black;
                padding: 27px 30px;
                &:nth-child(1) {
                    background-color: #E8DE86;
                }
                &:nth-child(2) {
                    background-color: #C2DCC7;
                    figure {
                        right: 10px;
                    }
                }
                &:nth-child(3) {
                    background-color: #C3D9D4;
                }
                figure {
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    width: 165px;
                }
                .txtarea {
                    color: black;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    height: 100%;
                    position: relative;
                    z-index: 2;
                    h4 {
                        font-family: YuGothic;
                        font-weight: bold;
                        font-size: 24px;
                        letter-spacing: 0.04em;
                        line-height: 1.5;
                    }
                    p {
                        font-family: YuGothic;
                        font-weight: bold;
                        font-size: 15px;
                        line-height: 1.5;
                    }
                    .c-link {
                        margin-top: 9px;
                    }
                }
            }
        }
    }
    &-content {
        .title {
            border: 2px solid black;
            padding: 65px 70px;
            position: relative;
            p {
                font-family: YuGothic;
                font-weight: bold;
                font-size: 22px;
                letter-spacing: 0.2em;
                line-height: 1.2;
                z-index: 2;
                position: relative;
            }
            h2 {
                font-family: YuGothic;
                font-weight: bold;
                font-size: 40px;
                letter-spacing: 0.04em;
                line-height: 1.2;
                z-index: 2;
                position: relative;
            }
            figure {
                display: inline-block;
                position: absolute;
                bottom: 0;
                right: 0;
                width: 385px;
                z-index: 1;
            }
        }
        .list {
            border: solid black;
            border-width: 0 2px 2px 2px;
            padding: 60px 72px 88px;
            .item {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                &.reverse {
                    flex-direction: row-reverse;
                    .imgarea {
                        padding-left: 0;
                        padding-right: 30px;
                    }
                }
                .txtarea {
                    width: calc(100% - 400px);
                    padding: 50px 20px;
                    .point {
                        p {
                            display: inline-flex;
                            background-color: black;
                            color: white;
                            padding: 3px 10px;
                            span {
                                &:first-child {
                                    font-family: $font_futura;
                                    font-weight: 500;
                                    font-style: italic;
                                    font-size: 14px;
                                    letter-spacing: 0.09em;
                                    line-height: (18 / 14);
                                    margin-right: 9px;
                                }
                                &:last-child {
                                    font-family: $font_futura;
                                    font-weight: 500;
                                    font-style: italic;
                                    font-size: 18px;
                                    letter-spacing: 0.1em;
                                    line-height: 1;
                                }
                            }
                        }
                    }
                    h3 {
                        font-family: YuGothic;
                        font-weight: bold;
                        font-size: 24px;
                        line-height: (32 / 24);
                        margin-top: 19px;
                    }
                    .desc {
                        font-family: YuGothic;
                        font-weight: 500;
                        font-size: 15px;
                        line-height: (27 / 15);
                        margin-top: 17px;
                    }
                }
                .imgarea {
                    width: 400px;
                    padding-left: 30px;
                }
            }
        }
        .c-article_list .c-list .item {
            width: calc((100% - 105px) / 4);
            margin-bottom: 30px;
        }
        .c-list .item .desc {
            font-size: 14px;
        }
    }
}

.p-movingguide {
    &Index {
        .list {
            .item {
                width: 100%;
                padding: 42px 40px 18px;
                position: relative;
                border: 2px solid black;
                display: block;
                &:not(:last-child) {
                    margin-bottom: 20px;
                }
                .txtarea {
                    position: relative;
                    z-index: 1;
                    h2 {
                        font-family: YuGothic;
                        font-weight: bold;
                        font-size: 28px;
                        letter-spacing: 0.04em;
                        line-height: 1.2;
                    }
                    p {
                        font-family: YuGothic;
                        font-weight: bold;
                        font-size: 18px;
                        line-height: 1.2;
                        margin-bottom: 5px;
                    }
                    .c-link {
                        margin-top: 20px;
                    }
                }
                &:first-child {
                    background-color: #E8DE86;
                }
                &:nth-child(2) {
                    background-color: #ACBDE5;
                }
                &:nth-child(3) {
                    background-color: #BCD5BE;
                    figure {
                        width: 150px;
                    }
                }
                &:nth-child(4) {
                    background-color: #C3D9D4;
                    figure {
                        width: 150px;
                    }
                }
                figure {
                    display: inline-block;
                    position: absolute;
                    bottom: 0px;
                    right: 10px;
                    width: 230px;
                }
            }
        }
    }
    &Pro {
        .p-movingguide-content {
            .title {
                background-color: #F0E68C;
            }
            .list {
                background-color: white;
            }
        }
        .otherinfo {
            .list {
                .item {
                    &:nth-child(1) {
                        background-color: #ACBDE5;
                        figure {
                            width: 120px;
                        }
                    }
                    &:nth-child(2) {
                        background-color: #BCD5BE;
                        figure {
                            width: 120px;
                        }
                    }
                    &:nth-child(3) {
                        background-color: #C3D9D4;
                        figure {
                            width: 120px;
                            right: 15px;
                            bottom: 10px;
                        }
                    }
                }
            }
        }
    }
    &Contra {
        .p-movingguide-content {
            .title {
                background-color: #ACBDE5;
                figure {
                    width: 300px;
                    bottom: -4px;
                    right: 35px;
                }
            }
            .list {
                background-color: #E6E6E6;
                .item {
                    align-items: center;
                    .txtarea .ok {
                        margin-top: 24px;
                        display: flex;
                        padding: 24px 20px;
                        font-family: YuGothic;
                        font-weight: bold;
                        font-size: 14px;
                        line-height: (24 / 14);
                        background-color: white;
                        border-radius: 6px;
                        figure {
                            width: 60px;
                        }
                        p {
                            width: calc(100% - 60px);
                            padding-left: 20px;
                        }
                    }
                }
            }
        }
        .otherinfo {
            .list {
                .item {
                    &:nth-child(1) {
                        background-color: #E8DE86;
                    }
                    &:nth-child(2) {
                        background-color: #BCD5BE;
                        figure {
                            width: 120px;
                        }
                    }
                    &:nth-child(3) {
                        background-color: #C3D9D4;
                        figure {
                            width: 120px;
                            right: 15px;
                            bottom: 10px;
                        }
                    }
                }
            }
        }
    }
    &Support {
        .c-content1 {
            .title {
                font-family: YuGothic;
                font-weight: bold;
                font-size: 26px;
                letter-spacing: 0.02em;
                line-height: 1.5;
                margin-bottom: 26px;
            }
            .item {
                position: relative;
                background-color: #C2DCC7;
                margin-bottom: 20px;
                &::before {
                    width: 0;
                    height: 0;
                    border-top: 0px solid transparent;
                    border-right: 30px solid #F7F7F4;
                    border-bottom: 30px solid transparent;
                    content: "";
                    top: 0;
                    right: 0;
                    position: absolute;
                }
                &_title {
                    font-family: YuGothic;
                    font-weight: bold;
                    font-size: 22px;
                    line-height: (28 / 22);
                    padding: 34px 30px 5px;
                    border-bottom: 2px solid black;
                }
                &_content {
                    display: flex;
                    flex-wrap: wrap;
                    padding: 30px 30px 38px 40px;
                }
                &_left {
                    width: 540px;
                }
                &_right {
                    width: calc(100% - 540px);
                    padding-left: 40px;
                    h3 {
                        font-family: YuGothic;
                        font-weight: bold;
                        font-size: 20px;
                        line-height: (28 / 20);
                    }
                    .desc {
                        font-family: YuGothic;
                        font-weight: 500;
                        font-size: 14px;
                        letter-spacing: 0.06em;
                        line-height: (22 / 14);
                        padding: 26px 0 15px;
                    }
                    .txt {
                        font-family: YuGothic;
                        font-weight: 500;
                        font-size: 13px;
                        letter-spacing: 0.06em;
                        line-height: (20 / 13);
                    }
                    a {
                        word-break: break-all;
                    }
                }
            }
        }
        .otherinfo {
            .list {
                .item {
                    &:nth-child(1) {
                        background-color: #E8DE86;
                    }
                    &:nth-child(2) {
                        background-color: #ACBDE5;
                        figure {
                            width: 130px;
                        }
                    }
                    &:nth-child(3) {
                        background-color: #C3D9D4;
                        figure {
                            width: 120px;
                            right: 15px;
                            bottom: 10px;
                        }
                    }
                }
            }
        }
    }
    &Realestate {
        .title {
            font-family: YuGothic;
            font-weight: bold;
            font-size: 26px;
            letter-spacing: 0.1em;
            line-height: 1.5;
        }
        .panel {
            margin-top: 26px;
            background-color: #C3D9D4;
            &_title {
                padding: 30px 38px 0;
                display: flex;
                align-items: center;
                border-bottom: 2px solid black;
                h3 {
                    font-family: $font_futura;
                    font-weight: 500;
                    font-style: italic;
                    font-size: 28px;
                    letter-spacing: 0.1em;
                    line-height: (44 / 28);
                }
                p {
                    font-family: YuGothic;
                    font-weight: bold;
                    font-size: 15px;
                    letter-spacing: 0.1em;
                    line-height: 1.5;
                    margin-left: 28px;
                }
            }
            &_content {
                padding: 27px 30px 30px;
            }
            &-group {
                padding-bottom: 24px;
                border-bottom: 2px solid white;
                margin-bottom: 20px;
                &_title {
                    display: flex;
                    align-items: center;
                    margin-bottom: 5px;
                    h4 {
                        font-family: YuGothic;
                        font-weight: bold;
                        font-size: 15px;
                        letter-spacing: 0.06em;
                        line-height: 2;
                    }
                    p {
                        font-family: YuGothic;
                        font-weight: 500;
                        font-size: 13px;
                        line-height: (20 / 13);
                        margin-left: 30px;
                    }
                }
                &-row {
                    display: flex;
                    align-items: center;
                    &_head {
                        font-family: YuGothic;
                        font-weight: bold;
                        font-size: 14px;
                        line-height: (30 / 14);
                        margin-right: 34px;
                    }
                    .u-checkbox {
                        margin-bottom: 0;
                    }
                    &_item {
                        display: inline-block;
                        &:not(:last-child) {
                            margin-right: 34px;
                        }
                    }
                }
            }
            .btn {
                text-align: center;
            }
        }
        .group-title {
            font-family: YuGothic;
            font-weight: bold;
            font-size: 22px;
            line-height: (27 / 22);
            border-bottom: 2px solid black;
            margin-top: 80px;
        }
        .c-list {
            margin-top: 40px;
            .item {
                width: calc((100% - 58px) / 3);
                margin-bottom: 38px;
                .category {
                    margin-bottom: 0;
                    span {
                        background-color: white;
                        padding: 2px 5px;
                    }
                }
            }
        }
        .otherinfo {
            .c-content {
                padding-top: 0;
            }
            .list {
                flex-wrap: wrap;
                .item {
                    width: calc((100% - 20px) / 2);
                    &:nth-child(1) {
                        background-color: #E8DE86;
                        figure {
                            width: 180px;
                        }
                    }
                    &:nth-child(2) {
                        background-color: #ACBDE5;
                        figure {
                            width: 125px;
                        }
                    }
                    &:nth-child(3) {
                        background-color: #BCD5BE;
                        width: 100%;
                        margin-top: 20px;
                        .txtarea {
                            flex-direction: row;
                            .c-link {
                                margin-top: 0;
                            }
                        }
                        figure {
                            right: 174px;
                            width: 100px;
                        }
                    }
                }
            }
        }
    }
    @include mq(sp) {
        &-content {
            .title {
                padding: 46px 0 135px;
                text-align: center;
                p {
                    font-size: 18px;
                }
                h2 {
                    font-size: 28px;
                    margin-top: 6px;
                }
                figure {
                    width: 265px;
                    right: unset;
                    left: 50%;
                    transform: translateX(-50%);
                }
            }
            .list {
                border: solid black;
                padding: 0 30px;
                border: unset;
                .inner {
                    padding: 0 30px;
                    border: solid black;
                    border-width: 0 2px 2px 2px;
                }
                .item {
                    padding: 50px 0 0;
                    .txtarea {
                        width: 100%;
                        padding: 0 0 20px 0px;
                    }
                    .imgarea {
                        width: 100%;
                        padding: 0 25px !important;
                    }
                }
            }
        }
        .otherinfo {
            .list .item {
                width: 100%;
                &:not(:last-child) {
                    margin-bottom: 20px;
                }
            }
            .u-inner {
                padding: 0 20px;
            }
            .title p {
                margin-left: 0;
            }
        }
        &Index {
            .c-section-title h2 {
                font-size: 37px;
            }
            .list .item {
                padding: 35px 30px 18px;
            }
            .spAd {
                padding: 0;
                &.spAd1 {
                    padding: 60px 0 30px;
                }
            }
        }
        &Pro {
            .c-title {
                display: none;
            }
            .c-content1 {
                margin-top: 0;
            }
            .c-article_list .c-list .item {
                padding-bottom: 0;
                margin-bottom: 20px;
            }
        }
        &Contra {
            .c-title {
                display: none;
            }
            .c-content1 {
                margin-top: 0;
            }
            .c-article_list .c-list .item {
                padding-bottom: 0;
                margin-bottom: 20px;
            }
            .p-movingguide-content {
                .title figure {
                    width: 238px;
                }
                .list {
                    background-color: white;
                    .inner {
                        background-color: #E6E6E6;
                    }
                    .item .txtarea .ok {
                        padding: 10px 10px;
                        p {
                            width: calc(100% - 50px);
                            padding-left: 10px;
                        }
                        figure {
                            width: 50px;
                        }
                    }
                }
            }
        }
        &Support {
            .c-content1 {
                padding: 0 30px;
                .item {
                    &_content {
                        padding: 20px;
                    }
                    &_left {
                        width: 100%;
                    }
                    &_right {
                        width: 100%;
                        padding: 20px 0 0;
                    }
                }
            }
        }
        &Realestate {
            .panel_title {
                flex-direction: column;
                align-items: flex-start;
                p {
                    margin-left: 0;
                }
                h3 {
                    font-size: 25px;
                }
                padding: 30px 30px 10px;
            }
            .panel-group {
                &-row {
                    align-items: flex-start;
                    &_head {
                        min-width: 50px;
                        line-height: 1;
                        margin-right: 15px;
                    }
                    &_content {
                        width: calc(100% - 50px);
                        display: flex;
                        flex-wrap: wrap;
                    }
                    &_item {
                        &:not(:last-child) {
                            margin: 0 0 15px 0;
                        }
                        width: 50%;
                    }
                }
                &.last {
                    .panel-group-row {
                        flex-wrap: wrap;
                    }
                }
            }
            .c-content {
                .column1 {
                    padding: 0 30px;
                }
            }
            .group-title {
                font-size: 20px;
                margin-top: 0;
                &.first {
                    margin-top: 60px;
                }
            }
            .c-list .item {
                width: calc((100% - 20px) / 2);
                margin-bottom: 30px;
            }
            .spAd {
                padding-top: 20px;
            }
            .otherinfo .list .item:nth-child(3) {
                margin-top: 0;
                .txtarea {
                    flex-direction: column;
                }
                figure {
                    right: 0px;
                    width: 100px;
                }
            }
        }
    }
}

// =================================== movingguide index page end =====================================