.company {
    display: flex;
    justify-content: center;
    &-item {
        &-row {
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            &:not(:last-child) {
                margin-bottom: 30px;
            }
            &__ttl {
                width: 130px;
                p {
                    font-family: YuGothic;
                    font-weight: bold;
                    font-size: 15px;
                    line-height: 28px;
                }
            }
            &__txt {
                p {
                    display: flex;
                    font-family: YuGothic;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 27px;
                    a {
                        color: #55BBD8;
                    }
                    span {
                        width: 100px;
                    }
                }
            }
        }
    }
}

@include mq(sp) {
    .company {
        .container {
            padding: 0 20px;
        }
    }
}