.contact-complete {
    text-align: center;
    .txt {
        font-family: YuGothic;
        font-weight: 500;
        font-size: 15px;
        line-height: 27px;
        margin-bottom: 49px;
    }
}

.contact-confirm {
    &>p {
        margin-bottom: 58px;
        font-family: YuGothic;
        font-weight: 500;
        font-size: 15px;
        line-height: 27px;
        text-align: center;
        color: #000;
    }
    &-row {
        margin-top: 35px;
        p:nth-child(1) {
            font-family: YuGothic;
            font-weight: bold;
            font-size: 16px;
            line-height: 26px;
        }
        p:nth-child(2) {
            margin-top: 9px;
            font-family: YuGothic;
            font-weight: 500;
            font-size: 16px;
            line-height: 30px;
            span {
                display: block;
            }
        }
    }
    .btn {
        text-align: center;
        margin-top: 58px;
        button {
            &:first-child {
                margin-right: 30px;
            }
        }
    }
}

.p-contact {
    &Index {
        .alert {
            font-family: "Source Han Sans JP";
            font-weight: normal;
            font-size: 14px;
            line-height: (26 / 14);
            color: #f00;
            margin-top: 13px;
        }
        .form-group {
            &:not(:last-child) {
                margin-bottom: 40px;
            }
            label {
                margin-bottom: 11px;
                display: inline-block;
                font-family: YuGothic;
                font-weight: bold;
                font-size: 16px;
                line-height: (26 /16);
                span {
                    font-family: "Source Han Sans JP";
                    font-weight: normal;
                    font-size: 14px;
                    line-height: (26 / 14);
                    color: #FF0000;
                    margin-left: 7px;
                }
            }
            .alert {
                display: none;
            }
            &.is-invalid {
                .alert {
                    display: block;
                }
            }
        }
        .form-row {
            display: flex;
        }
        .name {
            .form-row {
                justify-content: space-between;
                flex-wrap: wrap;
            }
            .u-input {
                width: calc((100% - 20px) / 2);
            }
        }
        .phone {
            .form-row {
                align-items: center;
            }
            .u-input {
                width: 120px;
            }
            span {
                padding: 0 10px;
            }
        }
        .address {
            .form-row {
                margin-bottom: 10px;
            }
            .postal {
                align-items: center;
                flex-wrap: wrap;
                .u-input {
                    width: 122px;
                }
                span {
                    font-family: YuGothic;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 2;
                    margin-right: 5px;
                }
                p {
                    font-family: YuGothic;
                    font-weight: 500;
                    font-size: 13px;
                    line-height: (22 / 13);
                    margin-left: 20px;
                }
            }
            .prefecture {
                justify-content: space-between;
                flex-wrap: wrap;
                .u-select {
                    width: 265px;
                }
                .u-input {
                    width: calc(100% - 285px);
                }
            }
        }
        .message {
            textarea {
                width: 100%;
                height: 180px;
                border: 1px solid #D8D8D8;
            }
        }
        .radio {
            .u-radio {
                padding-left: 40px;
                margin-right: 66px;
            }
            label {
                font-family: YuGothic;
                font-weight: 500;
                font-size: 15px;
                line-height: (25 / 15);
            }
        }
        .u-checkbox {
            margin-top: 20px;
        }
        .btn {
            text-align: center;
            margin-top: 60px;
        }
    }
    @include mq(sp) {
        &Index {
            .c-content1 {
                padding: 0 20px;
            }
            .name {
                .form-row {
                    flex-direction: column;
                }
                .u-input {
                    width: 100%;
                    &:first-child {
                        margin-bottom: 10px;
                    }
                }
            }
            .address {
                .prefecture {
                    flex-direction: column;
                    .u-input {
                        width: 100%;
                    }
                    .u-select {
                        &:first-child {
                            margin-bottom: 10px;
                        }
                    }
                }
                .postal p {
                    margin: 10px 0 0;
                }
            }
            .phone .u-input {
                width: 85px;
            }
            .radio .u-radio {
                padding-left: 40px;
                margin-right: 32px;
            }
        }
    }
}

@include mq(sp) {
    .contact-confirm {
        .container {
            padding: 0 20px;
        }
        .c-link.sm {
            width: 150px;
        }
        .btn button:first-child {
            margin-right: 20px;
        }
    }
    .contact-complete {
        .container {
            padding: 0 20px;
            .txt {
                text-align: left;
            }
        }
    }
}