// =================================== common footer start =====================================
.l-footer {
    color: white;
    a {
        color: white;
        @include hover_opacity();
    }
    .top {
        background-color: #00BEDC;
        text-align: center;
        padding: 40px 0 18px;
        .bgmenu {
            text-align: center;
            margin-top: 27px;
            ul {
                display: flex;
                justify-content: center;
                &:first-child {
                    margin-bottom: 11px;
                }
                li {
                    display: flex;
                    align-items: center;
                    font-family: YuGothic;
                    font-weight: bold;
                    font-size: 14px;
                    letter-spacing: 0.08em;
                    line-height: (20 / 14);
                    &:not(:last-child) {
                        &::after {
                            content: "";
                            width: 2px;
                            height: 14px;
                            display: block;
                            background-color: white;
                            margin: 0 20px;
                        }
                    }
                }
            }
        }
        .sns {
            margin: 24px 0 27px;
            ul {
                display: flex;
                justify-content: center;
                li {
                    &:not(:last-child) {
                        margin-right: 12px;
                    }
                }
            }
        }
        .smmenu {
            ul {
                display: flex;
                justify-content: center;
                li {
                    a {
                        font-family: YuGothic;
                        font-weight: bold;
                        font-size: 12px;
                        letter-spacing: 0.02em;
                        line-height: (28 / 12);
                    }
                    &:not(:last-child) {
                        margin-right: 16px;
                    }
                }
            }
        }
    }
    .bottom {
        background-color: black;
        padding: 12.5px 0;
        .u-inner {
            display: flex;
            justify-content: space-between;
        }
        p {
            font-family: YuGothic;
            font-weight: 500;
            font-size: 11px;
            line-height: (21 / 11);
        }
        ul {
            display: flex;
            li {
                &:not(:last-child) {
                    margin-right: 20px;
                }
                font-family: $font_futura;
                font-weight: 500;
                font-size: 13px;
                letter-spacing: 0.08em;
                line-height: (20 / 13);
            }
        }
    }
}

@include mq(sp) {
    .l-footer {
        padding-top: 80px;
        .top {
            .bgmenu ul {
                flex-wrap: wrap;
                justify-content: space-between;
                padding: 0 40px 0 50px;
                &:first-child {
                    margin-bottom: 0;
                }
                li {
                    width: 50%;
                    margin-bottom: 15px;
                    &:not(:last-child)::after {
                        content: unset;
                    }
                }
            }
            .smmenu ul {
                flex-wrap: wrap;
                li {
                    width: 50%;
                    &:not(:last-child) {
                        margin-right: 0;
                    }
                    &:nth-child(1) {
                        text-align: right;
                        padding-right: 8px;
                    }
                    &:nth-child(3) {
                        text-align: right;
                        padding-right: 8px;
                    }
                    &:nth-child(2) {
                        text-align: left;
                        padding-left: 8px;
                    }
                    &:nth-child(4) {
                        text-align: left;
                        padding-left: 8px;
                    }
                }
            }
        }
        .bottom {
            .u-inner {
                flex-wrap: wrap;
                flex-direction: column-reverse;
                align-items: center;
                padding: 0 60px;
            }
            ul {
                flex-wrap: wrap;
                justify-content: flex-start;
                li {
                    &:nth-child(1) {
                        width: 100%;
                    }
                    margin-bottom: 8px;
                }
                p {
                    margin-top: 8px;
                }
            }
        }
    }
}

// =================================== common footer end =====================================