// =================================== top page start =====================================
.p-top {
    .l-header {
        position: fixed;
    }
    &Fv {
        .banner {
            position: relative;
            .title {
                position: absolute;
                top: 50%;
                right: 10%;
                display: flex;
                flex-direction: column;
                align-items: flex-end;
                h3 {
                    font-weight: bold;
                    font-size: 30px;
                    text-align: right;
                    color: #fff;
                    background-color: #00BEDC;
                    display: inline-block;
                    &:first-child {
                        padding: 2px 20px 2px 35px;
                        margin-bottom: 8px;
                    }
                    &:last-child {
                        padding: 2px 20px 2px 35px;
                    }
                }
            }
            .item img {
                width: 100%;
            }
        }
        .article {
            padding-left: 80px;
            margin-top: -45px;
            .item {
                margin-right: 16px;
                background-color: white;
                width: 230px;
                position: relative;
                img {
                    @include hover_opacity();
                }
                .txt {
                    padding: 44px 20px 14px;
                    .desc {
                        font-weight: bold;
                        font-size: 15px;
                        line-height: (22 / 15);
                        color: #000;
                    }
                }
                &.c-category::before {
                    top: 10px;
                    left: 0;
                }
            }
        }
        .search {
            display: flex;
            flex-direction: column;
            align-items: center;
            background-color: #F7F7F4;
            padding: 40px 0;
            form {
                .search_input {
                    position: relative;
                    input {
                        width: 544px;
                        font-family: YuGo-Medium-83pv-RKSJ-H;
                        font-weight: normal;
                        font-size: 15px;
                        letter-spacing: 0.1em;
                        line-height: (26 / 15);
                        outline: unset;
                        background-color: white;
                        border: unset;
                        padding: 11px 12px;
                        &::placeholder {
                            color: #B3B3B3;
                        }
                    }
                    button {
                        position: absolute;
                        top: 50%;
                        right: 20px;
                        transform: translateY(-50%);
                        @include hover_opacity();
                        cursor: pointer;
                        outline: unset;
                        border: unset;
                        background-color: unset;
                        display: flex;
                        padding: 0;
                    }
                }
            }
            .keyword {
                display: flex;
                align-items: center;
                margin-top: 20px;
                .lead {
                    font-family: $font_futura;
                    font-weight: 500;
                    font-style: italic;
                    font-size: 18px;
                    letter-spacing: 0.1em;
                    line-height: (30 / 18);
                    color: #000;
                }
                ul {
                    display: flex;
                    a {
                        font-family: YuGo-Medium-83pv-RKSJ-H;
                        font-weight: normal;
                        font-size: 12px;
                        letter-spacing: 0.1em;
                        line-height: (21 / 12);
                        color: #808080;
                        border-bottom: 1px solid #808080;
                        display: block;
                    }
                    li {
                        margin-left: 20px;
                        &:first-child {
                            margin-left: 30px;
                        }
                    }
                }
            }
        }
    }
    &Topic {
        .c-link {
            display: block;
        }
    }
    &Feature {
        margin-top: 167px;
        background-color: #F7F7F4;
        padding-bottom: 40px;
        position: relative;
        .title {
            text-align: center;
            width: 100%;
            position: absolute;
            top: -50px;
            h1 {
                font-family: $font_futura;
                font-weight: 500;
                font-style: italic;
                font-size: 58px;
                letter-spacing: 0.1em;
                line-height: (74 / 58);
            }
            p {
                font-family: YuGothic;
                font-weight: bold;
                font-size: 19px;
                letter-spacing: 0.2em;
                line-height: (26 / 19);
            }
        }
        .slider {
            padding-top: 99px;
            .item {
                width: 540px;
                padding: 0 35px;
                .txt {
                    font-family: YuGothic;
                    font-weight: bold;
                    font-size: 24px;
                    letter-spacing: 0.05em;
                    line-height: (36 / 24);
                    padding: 45px 60px 37px;
                    display: inline-block;
                    background-color: white;
                    margin-left: -20px;
                    margin-top: -50px;
                    position: relative;
                    &.c-category {
                        &::before {
                            content: "FEATURE";
                            top: 17px;
                            left: 0;
                        }
                    }
                }
            }
        }
        .slick-arrow {
            top: 44%;
        }
        .slick-slide {
            opacity: 0.5;
            &.slick-current {
                opacity: 1;
            }
        }
        .slick-prev {
            left: calc(25% - 10px);
            &:before {
                content: url(../img/top/feature_arrow.svg);
                left: 0px;
            }
        }
        .slick-next {
            right: calc(25% - 10px);
            &:before {
                content: url(../img/top/feature_arrow.svg);
                transform: rotate(180deg);
                left: unset;
                right: 0;
            }
        }
        .link {
            text-align: center;
            margin-top: 47px;
        }
    }
    &Event {
        .advertise {
            text-align: center;
            margin: 80px 0 98px;
        }
        .c-content {
            padding-top: 40px;
            .column1 {
                width: 668px;
                .item {
                    width: 313px;
                }
            }
            .column2 {
                width: 318.67px;
            }
        }
    }
    &Spot {
        padding-top: 74px;
        .content {
            background-color: #F7F7F4;
            margin-top: 34px;
            .map {
                display: flex;
            }
            .column {
                width: 50%;
                position: relative;
                &:last-child {
                    margin-top: 43px;
                }
                dl {
                    padding: 17px 0 8px;
                    &:not(:last-child) {
                        border-bottom: 2px solid white;
                    }
                    dt {
                        display: flex;
                        padding-bottom: 20px;
                        h4 {
                            font-family: YuGothic;
                            font-weight: bold;
                            font-size: 18px;
                            letter-spacing: 0.06em;
                            line-height: (20 / 18);
                        }
                        p {
                            font-family: YuGothic;
                            font-weight: 500;
                            font-size: 13px;
                            line-height: (20 / 13);
                            margin-left: 20px;
                        }
                    }
                    dd {
                        display: flex;
                        flex-wrap: wrap;
                        .item {
                            width: calc(100% / 3);
                        }
                    }
                }
                .link {
                    text-align: center;
                    margin-top: 22px;
                    .c-link {
                        width: 306px;
                    }
                }
            }
            .c-list {
                padding: 0 33px;
                .item {
                    width: 225px;
                    .category span {
                        background-color: white;
                    }
                }
            }
            .c-label::after {
                background-color: #F7F7F4;
            }
        }
    }
    &Guide {
        padding-top: 121px;
        .list {
            display: flex;
            justify-content: space-between;
            .item {
                width: calc((100% - 24) / 2);
                position: relative;
                display: block;
                .txt {
                    position: absolute;
                    top: 40px;
                    left: 50px;
                    color: white;
                    h3 {
                        font-family: $font_futura;
                        font-weight: 500;
                        font-style: italic;
                        font-size: 32px;
                        letter-spacing: 0.1em;
                        line-height: (36 / 32);
                    }
                    p {
                        font-family: YuGothic;
                        font-weight: bold;
                        font-size: 18px;
                        letter-spacing: 0.2em;
                        line-height: 1.2;
                        text-align: left;
                        color: #fff;
                        margin-top: 5px;
                    }
                    .c-link {
                        margin-top: 26.5px;
                    }
                }
                img {
                    @include hover_opacity();
                }
            }
        }
    }
    &Sns {
        padding-top: 120px;
        .list {
            display: flex;
            justify-content: center;
            .item {
                width: 346px;
                &:first-child {
                    margin-right: 100px;
                }
                .title {
                    display: flex;
                    justify-content: center;
                    margin-bottom: 11px;
                    h3 {
                        font-family: $font_futura;
                        font-weight: 500;
                        font-style: italic;
                        font-size: 22px;
                        line-height: (36/ 22);
                        text-align: center;
                        margin-left: 9px;
                    }
                }
            }
            .imglist {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                .imgitem {
                    width: calc((100% - 16px) / 3);
                    margin-bottom: 8px;
                    figure {
                        display: flex;
                    }
                }
            }
            .link {
                margin-top: 17.8px;
                text-align: center;
                .c-link {
                    width: 130px;
                }
            }
        }
    }
    &Company {
        text-align: center;
        margin-top: 140px;
        background-color: #F7F7F4;
        position: relative;
        .title {
            top: -25px;
            position: absolute;
            width: 100%;
            h3 {
                font-family: $font_futura;
                font-weight: 500;
                font-style: italic;
                font-size: 28px;
                letter-spacing: 0.1em;
                line-height: (36 / 28);
                text-align: center;
            }
            p {
                font-family: YuGothic;
                font-weight: bold;
                font-size: 14px;
                letter-spacing: 0.06em;
                line-height: 1.5;
            }
        }
        .content {
            padding: 60px 0 50px;
            .list {
                display: flex;
                justify-content: center;
                &:first-child {
                    margin-bottom: 10px;
                }
                .item {
                    width: 230px;
                    height: 90px;
                    background: #fff;
                    border: 1px solid #e6e6e6;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    &:not(:last-child) {
                        margin-right: 10px;
                    }
                    @include hover_opacity();
                }
            }
        }
    }
    @include mq(sp) {
        .l-header {
            position: sticky;
        }
        &Fv {
            background-color: #F7F7F4;
            .article {
                padding-left: 20px;
                margin-top: -35px;
            }
            .banner .title {
                top: 50px;
                right: 20px;
                h3 {
                    font-size: 20px;
                    &:last-child {
                        padding: 2px 20px 2px 50px;
                    }
                    &:first-child {
                        padding: 2px 20px 2px 30px;
                    }
                }
            }
            .search {
                padding: 40px 20px 50px;
                .keyword {
                    flex-direction: column;
                    justify-content: center;
                    margin-top: 14px;
                    ul {
                        width: 190px;
                        margin-top: 8px;
                        flex-wrap: wrap;
                        justify-content: space-between;
                        li {
                            margin: 0 0 8px 0;
                            &:first-child {
                                margin-left: 0;
                            }
                        }
                    }
                    .lead {
                        font-size: 14px;
                    }
                }
                form {
                    width: 100%;
                    .search_input input {
                        width: 100%;
                    }
                }
            }
            .article {
                .item {
                    width: 150px;
                    .txt {
                        padding: 30px 13px 10px;
                        .desc {
                            font-size: 13px;
                        }
                        .c-date {
                            margin-top: 20px;
                        }
                    }
                    &.c-category::before {
                        top: 0;
                    }
                }
            }
        }
        &Topic {
            .c-content {
                padding-top: 0;
            }
        }
        &Feature {
            margin-top: 50px;
            .slider .item {
                width: 332px;
                padding: 0 5px;
                .txt {
                    margin: 0;
                    display: block;
                    font-size: 21px;
                    padding: 45px 35px 33px 35px;
                }
            }
            .slick-arrow {
                top: 102px;
            }
            .slick-prev {
                left: 9px;
                &:before {
                    content: url(../img/top/feature_sp_arrow.svg);
                    left: 0px;
                }
            }
            .slick-next {
                right: 9px;
                &:before {
                    content: url(../img/top/feature_sp_arrow.svg);
                    transform: rotate(180deg);
                    left: unset;
                    right: 0;
                }
            }
            .link {
                margin-top: 29px;
                .c-link {
                    width: 260px;
                }
            }
        }
        &Event {
            padding: 60px 17px 0;
            .c-content {
                flex-direction: column-reverse;
                .column1 {
                    margin-top: 40px;
                    .c-list {
                        justify-content: space-around;
                    }
                }
                .column2 form {
                    padding: 37px 38px 40px;
                }
            }
        }
        &Spot {
            padding: 0 16.5px;
            .c-map-piece {
                height: 275px;
                .inner {
                    zoom: 0.68;
                }
            }
            .content {
                .map {
                    flex-direction: column;
                    .column {
                        width: 100%;
                        &:last-child {
                            padding: 0 30px;
                        }
                        dl dd .item {
                            width: calc(100% / 2);
                        }
                        .link {
                            .c-link {
                                width: 242px;
                            }
                        }
                    }
                }
                .c-list {
                    padding: 0 14px;
                    .item {
                        width: calc((100% - 14px) / 2);
                        .desc {
                            font-size: 14px;
                            margin: 13px 0 13px;
                        }
                        .category span {
                            font-size: 11px;
                        }
                        img {
                            width: 100%;
                        }
                    }
                }
            }
            .c-label {
                margin-top: 50px;
            }
            .sp-allview {
                text-align: right;
                margin-top: 17px;
            }
        }
        &Guide {
            padding: 60px 28px 0;
            .list {
                flex-wrap: wrap;
                .item {
                    width: 100%;
                    &:first-child {
                        margin-bottom: 29.5px;
                    }
                    .txt {
                        top: 40px;
                        left: 39px;
                    }
                    img {
                        width: 100%;
                    }
                }
            }
        }
        &Sns {
            padding: 0 10px 0;
            .list {
                flex-wrap: wrap;
                .item {
                    width: 100%;
                    padding-top: 60px;
                    &:first-child {
                        margin-right: 0;
                    }
                    &:last-child {
                        figure img {
                            width: 100%;
                        }
                    }
                }
                .imglist {
                    img {
                        width: 100%;
                    }
                }
            }
        }
    }
}

// =================================== top page end =====================================