.slick {
    &-arrow {
        background-color: #ffffff00;
        ;
        top: 38px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        z-index: 1;
    }
    &-prev {
        opacity: 40;
        left: -50px;
        &:before {
            content: url(../img/common/slick_arrow_previous.svg);
            position: absolute;
            left: 15px;
            top: 7px;
        }
    }
    &-next {
        background-color: #ffffff00;
        right: -50px;
        &:before {
            content: url(../img/common/slick_arrow_next.svg);
            position: absolute;
            left: 18px;
            top: 6px;
        }
    }
    &-slider {
        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    &-dots {
        bottom: -40px;
        li {
            margin: 0;
            button {
                padding: 3.5px;
                &:before {
                    font-size: 12px;
                    opacity: 1;
                    color: #1ebf7380;
                    @include mq(sp) {
                        font-size: 8px;
                    }
                }
            }
        }
        @include mq(sp) {
            bottom: -20px;
        }
    }
}

.slick-disabled {
    background-color: #6B717E!important;
}

.slick-disabled:hover,
.slick-disabled:focus,
.slick-disabled:active {
    background-color: #6B717E !important;
    opacity: 1!important;
}

.slick-arrow:hover,
.slick-arrow:focus {
    background-color: #ffffff00;
    ;
    opacity: 0.5;
}

.slick-dots li.slick-active button:before {
    opacity: .75;
    color: #1EBF73;
}

.slick-dotted.slick-slider {
    margin-bottom: 0;
}