// =================================== event index page start =====================================
.p-event {
    .c-title {
        background-image: url(../img/event/index/banner.jpg);
    }
}

.p-eventIndex {
    .c-form {
        margin-bottom: 67px;
        padding-top: 0;
        .date {
            align-items: flex-start;
        }
        .radio {
            display: flex;
            margin: 0 0 16px;
            .u-radio:first-child {
                margin-right: 30px;
            }
        }
        .during {
            display: flex;
            justify-content: space-between;
            align-items: center;
            p {
                margin: 0 12px;
            }
        }
    }
}

.p-eventArticle {
    .main {
        figure {
            margin-top: 22px;
        }
    }
    .shedule {
        padding: 30px 0;
        font-size: 14px;
        line-height: 1.5;
        color: #383838;
        font-weight: 500;
        border-bottom: 1px solid #E6E6E6;
        dl {
            display: flex;
            dt {
                font-family: YuGothic;
                width: 105px;
            }
            dd {
                font-family: Lato;
                letter-spacing: 0.06em;
                width: calc(100% - 105px);
                a {
                    word-break: break-all;
                }
            }
            &:not(:last-child) {
                margin-bottom: 18px;
            }
        }
    }
}

.p-eventSearch {
    .result {
        font-family: YuGothic;
        font-weight: bold;
        font-size: 16px;
        letter-spacing: 0.1em;
        line-height: 1.5;
    }
    .c-section-title {
        margin-bottom: 27px !important;
    }
}

@include mq(sp) {
    .p-eventIndex {
        .c-form {
            margin-bottom: 0;
        }
        .c-content {
            .pagination {
                margin-bottom: 0;
            }
        }
        &-spAd2 {
            padding-bottom: 0;
        }
    }
    .p-eventArticle {
        .c-content {
            padding-top: 0;
        }
        .shedule {
            dl {
                flex-wrap: wrap;
                dt {
                    width: 100%;
                }
                dd {
                    width: 100%;
                }
            }
        }
        .spAd {
            padding: 50px 27px 0;
        }
        &-spAd2 {
            padding-bottom: 0;
        }
    }
}

// =================================== event index page end =====================================