// =================================== base start =====================================
*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: YuGothic;
    font-size: 16px;
    color: black;
    overflow-x: hidden;
}

img {
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: unset;
    color: black;
}

// =================================== base end =====================================