// =================================== basicinfo index page start =====================================
.p-basicinfo {
    .c-title {
        background-image: url(../img/basicinfo/index/banner.jpg);
    }
    .otherinfo {
        position: relative;
        margin-top: 96px;
        .bg {
            width: 100%;
            height: 200px;
            position: absolute;
            top: -40px;
            background-color: #FFF7D6;
        }
        .title {
            color: black;
            display: flex;
            flex-wrap: wrap;
            align-items: baseline;
            position: relative;
            z-index: 2px;
            margin-bottom: 20px;
            h3 {
                font-family: $font_futura;
                font-weight: 500;
                font-style: italic;
                font-size: 30px;
                letter-spacing: 0.1em;
                line-height: 44px;
                text-align: left;
                color: #000;
            }
            p {
                font-family: YuGothic;
                font-weight: bold;
                font-size: 16px;
                letter-spacing: 0.1em;
                line-height: 1.5;
                margin-left: 30px;
            }
        }
        .list {
            position: relative;
            z-index: 2;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            .item {
                @include hover_opacity();
                width: calc((100% - 64px) / 3);
                position: relative;
                display: block;
                .txtarea {
                    position: absolute;
                    top: 30px;
                    left: 40px;
                    color: white;
                    h4 {
                        font-family: $font_futura;
                        font-weight: 500;
                        font-style: italic;
                        font-size: 30px;
                        letter-spacing: 0.1em;
                        line-height: (36 / 30);
                    }
                    p {
                        font-family: YuGothic;
                        font-weight: bold;
                        font-size: 16px;
                        letter-spacing: 0.2em;
                        line-height: 1.5;
                        margin-top: 5px;
                    }
                    .c-link {
                        margin-top: 18px;
                    }
                }
            }
        }
    }
}

.p-basicinfo {
    &-row {
        display: flex;
        .head {
            display: flex;
            align-items: center;
            background-color: #E6E7E6;
            font-family: YuGothic;
            font-weight: bold;
            font-size: 16px;
            line-height: (24 / 16);
        }
        .content {
            background-color: white;
            font-family: YuGothic;
            font-weight: 500;
            font-size: 16px;
            line-height: (24 / 16);
            .group {
                display: flex;
                .column {
                    padding: 18px 20px 13px 26px;
                    border: 1px solid #E6E6E6;
                    &:last-child {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        a {
                            font-family: YuGothic;
                            font-weight: 500;
                            font-size: 13px;
                            line-height: (17 / 13);
                            text-align: right;
                            color: #000;
                            display: inline-block;
                            padding: 0 0 3px 10px;
                            display: flex;
                            align-items: center;
                            border-bottom: 1px solid;
                            img {
                                margin-left: 10px;
                            }
                        }
                    }
                }
            }
        }
    }
    &-title {
        font-family: YuGothic;
        font-weight: bold;
        font-size: 22px;
        line-height: (27 / 15);
        text-align: left;
        color: #000;
        border-bottom: 2px solid black;
        padding-bottom: 5px;
    }
    &Index {
        .list {
            .item {
                display: block;
                position: relative;
                @include hover_opacity();
                &:not(:last-child) {
                    margin-bottom: 20px;
                }
                .txtarea {
                    position: absolute;
                    top: 48px;
                    left: 40px;
                    .title {
                        display: flex;
                        align-items: center;
                        h2 {
                            font-family: $font_futura;
                            font-weight: 500;
                            font-style: italic;
                            font-size: 30px;
                            letter-spacing: 0.1em;
                            line-height: (36 / 30);
                            color: #fff;
                            margin-right: 22px;
                        }
                        p {
                            font-family: YuGothic;
                            font-weight: bold;
                            font-size: 16px;
                            letter-spacing: 0.2em;
                            line-height: 1.5;
                            color: #fff;
                            margin-top: 5px;
                        }
                    }
                    .c-link {
                        margin-top: 20px;
                    }
                }
            }
        }
    }
    &About {
        .sec1 {
            .row {
                display: flex;
                align-items: center;
                margin-top: 50px;
                .column {
                    width: 50%;
                    &:first-child {
                        padding-right: 54px;
                    }
                    .txt {
                        font-family: YuGothic;
                        font-weight: 500;
                        font-size: 15px;
                        line-height: (27 / 15);
                        margin-top: 30px;
                    }
                }
            }
        }
        .sec2 {
            position: relative;
            margin-top: 154px;
            &_title {
                padding: 18px 0;
                width: 584px;
                font-family: YuGothic;
                font-weight: bold;
                font-size: 28px;
                line-height: 1;
                border: 2px solid black;
                text-align: center;
                position: absolute;
                top: 0;
                left: 50%;
                transform: translate(-50%, -50%);
                background-color: white;
                .circle {
                    position: absolute;
                    width: 10px;
                    height: 10px;
                    border: 1px solid black;
                    border-radius: 50%;
                    &.circle1 {
                        top: 10px;
                        left: 10px;
                    }
                    &.circle2 {
                        bottom: 10px;
                        left: 10px;
                    }
                    &.circle3 {
                        bottom: 10px;
                        right: 10px;
                    }
                    &.circle4 {
                        top: 10px;
                        right: 10px;
                    }
                }
            }
            .list {
                border: 2px solid black;
                padding: 30px 0 46px;
                background-color: #F7F7F4;
                .item {
                    padding: 50px 30px;
                    &:not(:last-child) {
                        border-bottom: 3px dashed black;
                    }
                    &_inner {
                        border: 6px solid white;
                        padding: 42px 64px;
                    }
                    .title {
                        display: flex;
                        align-items: center;
                        background-color: white;
                        .number {
                            display: flex;
                            background-color: black;
                            padding: 6px 20px;
                            span {
                                color: #fff;
                                &:first-child {
                                    font-family: $font_futura;
                                    font-weight: 500;
                                    font-style: italic;
                                    font-size: 20px;
                                    letter-spacing: 0.09em;
                                    line-height: (36 / 20);
                                }
                                &:last-child {
                                    font-family: $font_futura;
                                    font-weight: 500;
                                    font-style: italic;
                                    font-size: 38px;
                                    letter-spacing: 0.1em;
                                    line-height: (36 /38);
                                }
                            }
                        }
                        .txt {
                            font-family: YuGothic;
                            font-weight: bold;
                            font-size: 23px;
                            line-height: (32 / 23);
                            padding-left: 40px;
                        }
                    }
                    .desc {
                        margin: 30px 0 45px;
                        font-family: YuGothic;
                        font-weight: 500;
                        font-size: 15px;
                        line-height: (27 / 15);
                    }
                    .row {
                        display: flex;
                        justify-content: space-between;
                        .column {
                            width: calc( (100% - 60px) / 4);
                            figure {
                                display: flex;
                                @include hover_opacity();
                            }
                            p {
                                margin-top: 14px;
                                font-family: YuGothic;
                                font-weight: bold;
                                font-size: 13px;
                                line-height: (24 / 13);
                            }
                        }
                    }
                }
            }
        }
    }
    &Area {
        .order {
            width: 100%;
            border: 2px solid black;
            display: flex;
            align-items: center;
            cursor: pointer;
            span {
                display: inline-block;
                width: 40px;
                &:first-child {
                    font-family: $font_futura;
                    font-weight: 500;
                    font-style: italic;
                    font-size: 30px;
                    letter-spacing: 0.1em;
                    line-height: (41 / 30);
                    color: #fff;
                    background-color: black;
                    text-align: center;
                }
                &:last-child {
                    width: calc(100% - 40px);
                    font-family: YuGothic;
                    font-weight: bold;
                    font-size: 18px;
                    line-height: (27 / 18);
                    text-align: left;
                    color: #000;
                    padding-left: 20px;
                }
            }
        }
        .sec1 {
            background-color: #F7F7F4;
            padding: 0 70px 55px 50px;
            .row {
                display: flex;
                justify-content: space-between;
            }
            .c-map-piece {
                height: 500px;
                width: 583.33px;
                .item1 {
                    top: 15px;
                    left: 10px;
                }
                .item2 {
                    top: 0;
                    left: 110px;
                }
                .item3 {
                    top: 0;
                    left: 230px;
                }
                .item9 {
                    top: 70px;
                    left: 102px;
                }
                .item8 {
                    top: 185px;
                    left: 142px;
                }
                .item5 {
                    top: 280px;
                    left: 156px;
                }
                .item7 {
                    top: 201px;
                    left: 266px;
                }
                .item6 {
                    top: 225px;
                    left: 235px;
                }
                .item4 {
                    top: 244px;
                    left: 246px;
                }
                .item10 {
                    top: 24px;
                    left: 195px;
                    position: absolute;
                }
            }
            .list {
                padding-top: 40px;
                .order {
                    width: 242px;
                    &:not(:last-child) {
                        margin-bottom: 10px;
                    }
                    &:hover {
                        background-color: #00BEDC;
                        border: 2px solid #00BEDC;
                        span {
                            background-color: #00BEDC;
                            &:first-child {
                                background-color: #00BEDC;
                                color: white;
                                border-right: 1px solid white;
                            }
                            &:last-child {
                                color: white;
                            }
                        }
                    }
                }
            }
            .desc {
                font-family: YuGothic;
                font-weight: 500;
                font-size: 17px;
                line-height: 35px;
                margin-top: 30px;
            }
        }
        .sec2 {
            .order {
                border-width: 2px 2px 0 2px;
                span {
                    &:first-child {
                        font-size: 34px;
                        width: 77px;
                    }
                    &:last-child {
                        font-size: 22px;
                        width: calc(100% - 77px);
                    }
                }
            }
            .list {
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                .item {
                    width: calc((100% - 64px) / 2);
                    margin-bottom: 65px;
                    .group {
                        border: solid black;
                        border-width: 0 2px 2px 2px;
                        padding: 30px 35px;
                    }
                    figure {
                        display: flex;
                    }
                    img {
                        width: 100%;
                    }
                    .desc {
                        font-family: YuGothic;
                        font-weight: 500;
                        font-size: 15px;
                        line-height: (27 / 15);
                        text-align: left;
                        color: #000;
                    }
                    .row {
                        margin-top: 26px;
                        display: flex;
                        justify-content: space-between;
                        flex-wrap: wrap;
                        .column {
                            width: calc((100% - 24px) / 3);
                            p {
                                font-family: YuGothic;
                                font-weight: bold;
                                font-size: 13px;
                                line-height: (20 / 13);
                                margin-top: 10px;
                            }
                        }
                    }
                    .link {
                        text-align: end;
                        margin-top: 26px;
                    }
                }
            }
        }
    }
    &Access {
        .sec_desc {
            width: 100%;
            background-color: white;
            font-family: YuGothic;
            font-weight: bold;
            font-size: 22px;
            letter-spacing: 0.06em;
            line-height: 1.5;
            padding: 10px 0;
            margin-top: 100px;
            text-align: center;
        }
        .inner {
            padding: 0 60px 70px;
            background-color: #F7F7F4;
        }
        .map {
            text-align: center;
            padding-top: 10px;
        }
        .traffic {
            margin-top: 50px;
            .list {
                .item {
                    &:not(:last-child) {
                        margin-bottom: 60px;
                    }
                    .desc {
                        font-family: YuGothic;
                        font-weight: 500;
                        font-size: 15px;
                        line-height: (27 / 15);
                        margin-top: 20px;
                    }
                    .figure {
                        margin-top: 35px;
                    }
                    .imgarea {
                        margin-top: 40px;
                        &_inner {
                            display: flex;
                            flex-wrap: wrap;
                        }
                        .c-label {
                            margin-bottom: 16px;
                        }
                        .imgitem {
                            width: calc( (100% - 60px) / 4);
                            &_content {
                                @include hover_opacity();
                                display: block;
                            }
                            figure {
                                display: flex;
                            }
                            p {
                                margin-top: 14px;
                                font-family: YuGothic;
                                font-weight: bold;
                                font-size: 13px;
                                line-height: (24 / 13);
                            }
                        }
                    }
                }
            }
            .p-basicinfo-row.row1 {
                &.first {
                    margin: 25px 0 10px;
                }
                .head {
                    width: 120px;
                    padding-left: 20px;
                }
                .content {
                    width: calc(100% - 120px);
                    .column {
                        &:first-child {
                            width: 290px;
                        }
                        &:last-child {
                            width: calc(100% - 290px);
                        }
                    }
                }
            }
            .p-basicinfo-row.row2 {
                &.first {
                    margin-top: 30px;
                }
                margin-bottom: 7px;
                .head {
                    width: 408px;
                    padding-left: 28px;
                }
                .content {
                    width: calc(100% - 408px);
                    .column {
                        width: 100%;
                    }
                }
            }
        }
    }
    &Sos {
        .c-section-title {
            padding-bottom: 30px;
        }
        .inner {
            background-color: #F7F7F4;
            padding: 70px 60px;
            .p-basicinfo-row {
                margin-bottom: 10px;
                .head {
                    width: 188px;
                    padding-left: 30px;
                }
                .content {
                    width: calc(100% - 188px);
                    .column {
                        font-size: 14px;
                        padding: 10px 24px 10px 20px;
                        &:first-child {
                            width: 136px;
                        }
                        &:last-child {
                            width: calc(100% - 136px);
                        }
                        .detail {
                            padding: 0;
                        }
                    }
                }
            }
        }
        .p-basicinfo-title {
            margin: 75px 0 30px;
        }
        .note {
            font-family: YuGothic;
            font-weight: 500;
            font-size: 15px;
            line-height: (27 / 15);
            color: #f00;
            a {
                color: #f00;
                text-decoration: underline;
            }
        }
    }
    @include mq(sp) {
        .otherinfo {
            .title {
                padding: 0 30px;
                p {
                    margin-left: 0;
                }
            }
            .list {
                padding: 0 30px;
                .item {
                    width: 100%;
                    &:not(:last-child) {
                        margin-bottom: 20px;
                    }
                    img {
                        width: 100%;
                    }
                }
            }
        }
        &-row {
            flex-direction: column;
            .head {
                width: 100% !important;
                padding: 13px 20px !important;
                font-size: 18px;
            }
            .content {
                width: 100% !important;
                .group {
                    flex-direction: column;
                    padding: 20px;
                    border: 1px solid #E6E6E6;
                    .column {
                        width: 100% !important;
                        padding: 0px !important;
                        border: unset;
                        &:first-child {
                            p {
                                color: black;
                                font-weight: bold;
                            }
                        }
                        &:last-child {
                            flex-direction: column;
                            align-items: flex-end;
                        }
                        p {
                            width: 100%;
                            text-align: left;
                            margin-bottom: 10px;
                        }
                    }
                }
            }
        }
        &Index {
            .list .item .txtarea .title {
                flex-direction: column;
                align-items: flex-start;
            }
            .spAd {
                padding: 0;
                &.spAd1 {
                    padding: 60px 0 30px;
                }
            }
        }
        &About {
            .content {
                padding: 0 20px;
            }
            .sec1 .row {
                flex-wrap: wrap;
                .column {
                    width: 100%;
                    &:first-child {
                        padding-right: 0;
                        margin-bottom: 80px;
                    }
                }
            }
            .sec2 {
                margin-top: 100px;
                &_title {
                    padding: 12px 0;
                    width: 300px;
                    font-size: 24px;
                    line-height: 1.7;
                }
                .list .item {
                    padding: 40px 10px;
                    &_inner {
                        border: 2px solid white;
                        padding: 30px 15px;
                    }
                    .title {
                        flex-wrap: wrap;
                        background-color: unset;
                        .txt {
                            padding-left: 0;
                            background-color: white;
                            font-size: 18px;
                            line-height: 1.7;
                            padding: 15px;
                        }
                        .number {
                            span {
                                &:first-child {
                                    font-size: 12px;
                                    margin-right: 6px;
                                }
                                &:last-child {
                                    font-size: 26px;
                                }
                            }
                        }
                    }
                    .row {
                        flex-wrap: wrap;
                        .column {
                            width: calc( (100% - 20px) / 2);
                            margin-bottom: 15px;
                            .sp-control {
                                margin-top: 15px;
                            }
                        }
                    }
                    &:not(:last-child) {
                        border-bottom: 2px dashed black;
                    }
                }
            }
        }
        &Area {
            .c-section-title {
                padding: 0 20px;
            }
            .sec1 {
                padding: 0;
                margin-top: 30px;
                .row {
                    flex-wrap: wrap;
                    .column {
                        width: 100%;
                        &:first-child {
                            padding-right: 0;
                            margin-bottom: 80px;
                        }
                    }
                    .c-map-piece {
                        height: 310px;
                        width: 100%;
                        .inner {
                            zoom: 0.6;
                        }
                    }
                }
                .list {
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    padding: 30px 20px 0;
                    .order {
                        width: calc((100% - 10px) / 2);
                        span {
                            display: block;
                            font-size: 13px;
                            &:first-child {
                                padding: 4px 0;
                                font-size: 18px;
                            }
                            &:last-child {
                                width: 100%;
                                padding-left: 5px;
                            }
                        }
                    }
                }
                .desc {
                    padding: 0 20px;
                    font-size: 15px;
                }
            }
            .sec2 .list {
                padding: 45px 20px 0;
                .item {
                    width: 100%;
                    margin-bottom: 30px;
                    .group {
                        padding: 30px 25px;
                    }
                    .row {
                        flex-wrap: wrap;
                        .column {
                            width: calc( (100% - 20px) / 2);
                            margin-bottom: 15px;
                        }
                    }
                    .link {
                        margin-top: 15px;
                    }
                }
            }
        }
        &Access {
            .c-section-title {
                padding: 0 20px;
            }
            .inner {
                margin-top: 40px;
                padding: 0;
            }
            .map {
                figure {
                    padding: 0 20px;
                }
            }
            .traffic {
                .list {
                    .item {
                        padding: 0 20px;
                        .imgarea {
                            margin-top: 10px;
                            .c-label.sm {
                                width: 100%;
                                font-family: YuGothic;
                                font-weight: bold;
                                font-size: 18px;
                                letter-spacing: 0em;
                                line-height: (20 / 18);
                                color: #000;
                                margin: 0;
                                background-color: #E6E6E6;
                                padding: 13px 20px;
                                &:after {
                                    content: unset;
                                }
                            }
                            .imgitem {
                                width: 100%;
                                &_content {
                                    display: flex;
                                    align-items: center;
                                    padding: 13px 10px;
                                    border: 1px solid #E6E6E6;
                                    figure {
                                        width: 96px;
                                    }
                                    p {
                                        width: calc(100% - 96px);
                                        margin: 0 0 0 10px;
                                    }
                                }
                            }
                        }
                        &:nth-child(3) {
                            padding: 0;
                            .item_group {
                                padding: 0 20px;
                            }
                        }
                    }
                }
                // .p-basicinfo-title {
                //     padding: 0 20px;
                // }
            }
        }
        &Sos {
            .c-section-title {
                padding: 0 20px 30px;
            }
            .inner {
                padding: 50px 20px 0;
                .p-basicinfo-row .content .column {
                    &:last-child {
                        align-items: flex-start;
                    }
                }
            }
            .p-basicinfo-row .content {
                border: 1px solid #E6E6E6;
                padding: 20px;
                .group {
                    border: unset;
                    padding: unset;
                    &:not(:last-child) {
                        margin-bottom: 20px;
                    }
                }
            }
        }
    }
}

// =================================== basicinfo index page end =====================================