// =================================== feature index page start =====================================
.p-feature {
    .c-title {
        background-image: url(../img/feature/index/banner.jpg);
    }
}

.p-featureIndex {}

.p-featureSearch {
    .result {
        font-family: YuGothic;
        font-weight: bold;
        font-size: 16px;
        letter-spacing: 0.1em;
        line-height: 1.5;
    }
    .c-section-title {
        margin-bottom: 27px !important;
    }
}

@include mq(sp) {
    .p-featureIndex {
        .c-content {
            .column2 {
                .ranking {
                    margin-top: 60px;
                }
                .spAd {
                    padding-bottom: 0;
                }
            }
        }
        &-spAd2 {
            padding-bottom: 0;
        }
    }
    .p-featureSearch {
        .c-content {
            .column2 {
                .ranking {
                    margin-top: 60px;
                }
            }
        }
    }
}